import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';


export default function OwnQuetion(){
    return(
        <div className="container-fluid  mainContainer ">
        <div className="row lm90">
            <h1 className="mainText" style={{textAlign:"center",margin:'50px 0'}}>(OR)</h1>
        {/* <h4 className="mainText">If the above question's arent the one you want to know. enter your own question below</h4> */}
        {/* <textarea  className="mainText card-main" style={{fontWeight:600}} placeholder="    ,        "></textarea> */}
        <Box
      component="form"
      sx={{
        '& .MuiTextField-root': { m: 1, width: '100%' },
      }}
      noValidate
      autoComplete="off"
    >
        <TextField  multiline style={{  borderRadius: "10px", padding:'7px 3px'}}
          minRows={3} maxRows={5} fullWidth   size="normal" className="mainText card-main" margin='none' color='warning' id="outlined-multiline-flexible" label="       " variant="outlined" />
       </Box>
        <span style={{textAlign:'center', justifyContent:'center'}}>      
            <button type="submit" className="btn btn-success" style={{marginTop:'20px',width:'auto',fontSize:'1rem'}}>  </button>
        </span>
    </div>
  </div>
    )
}